@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-merriweather: Merriweather;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-5xl: 24px;
  --font-size-9xl: 28px;
  --font-size-xl: 20px;

  /* Colors */
  --color-lavender: #d0d9f4;
  --color-white: #fff;
  --color-darkblue: #061aa3;
  --color-black: #000;

  /* border radiuses */
  --br-xl: 20px;
}
